body{
  background-color: #505050;
}

.App{
  background-color: #252525;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: orangered;
  border: 0; 
  color: #fff;
  width: 220px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: #fff;
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: goldenrod; 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}
